import React,{useEffect} from 'react'
import AOS from 'aos'
import 'aos/dist/aos.css'

export default function Service() {
    useEffect(()=>{
        AOS.init({duration:1000})
    },[])
  return (
    <section className="services-area page-section scroll-to-page" id="services">
        <div className="custom-container">
            <div className="services-content content-width">
                <div className="section-header">
                    <h4 className="subtitle scroll-animation" data-aos='fade-up'>
                        <i className="las la-stream"></i> Services
                    </h4>
                    <h1 className="scroll-animation" data-aos='fade-up'>Mes <span>spcialisations</span></h1>
                </div>

                <div className="services-items">
                    <div className="service-item scroll-animation" data-aos='fade-up'>
                        <i className="las la-bezier-curve"></i>
                        <h2>Optimisation et amlioration des processus de travail</h2>
                        <p>Expert en optimisation, amlioration et automatisation des processus de travail</p>
                        <span className="Projets">30 Projets</span>
                    </div>
                    <div className="service-item scroll-animation" data-aos='fade-up'>
                        <i className="las la-code"></i>
                        <h2>Dveloppement</h2>
                        <p>Dveloppement d'outils d'automatisation et d'interfaces IHM pour une gestion efficace des composants dans la production, amliorant la visibilit et l'efficacit de la chane d'approvisionnement</p>
                        <span className="Projets">5 Projets</span>
                    </div>
                    <div className="service-item scroll-animation" data-aos='fade-up'>
                        <i className="las la-bezier-curve"></i>
                        <h2>Digitalisation</h2>
                        <p>Transformation numrique de l'usine en utilisant les donnes machine pour une collecte automatise et prcise des informations oprationnelles.</p>
                        <span className="Projets">8 Projets</span>
                    </div>
                    <div className="service-item scroll-animation" data-aos='fade-up'>
                        <i className="las la-bezier-curve"></i>
                        <h2>Expert MES</h2>
                        <p>je me spcialise dans l'optimisation des oprations de production pour amliorer l'efficacit et la performance globale de l'entreprise</p>
                        <span className="Projets">40 Projets</span>
                    </div>
                </div>    
            </div>
        </div>
    </section>
  )
}
