import React,{useEffect} from 'react'
import AOS from 'aos'
import 'aos/dist/aos.css'

export default function About() {
    useEffect(()=>{
        AOS.init({duration:1000})
    },[])
  return (  
    <section className="about-area page-section scroll-to-page" id="about">
        <div className="custom-container">
            <div className="about-content content-width">
                <div className="section-header">
                    <h4 className="subtitle scroll-animation" data-aos='fade-up'>
                        <i className="lar la-user"></i> a propos
                                            </h4>
                    <h1 className="scroll-animation" data-aos='fade-up'>Vers une ralit moderne<br/>
                     <span>L'impratif de la digitalisation et de l'intelligence artificielle</span></h1>
                </div>
                <p className="scroll-animation" data-aos='fade-up'>
                    Je suis un ingnieur en industrialisation et chef de projet MES spcialis dans la digitalisation et la smart industrie 4.0, la gestion de projet, les tests et la validation automatise, ainsi que le support technique. Fort d'une solide exprience dans le domaine de l'industrie, j'ai travaill sur de nombreux projets visant  optimiser les processus de fabrication et  intgrer les technologies numriques pour amliorer l'efficacit et la productivit.
                    </p> 
                <p className="scroll-animation" data-aos='fade-up'>
Ma passion pour la transformation numrique m'a conduit  me spcialiser dans la mise en uvre de systmes MES avancs, permettant une gestion transparente des oprations de fabrication et une prise de dcision claire en temps rel. J'ai une solide expertise dans l'analyse des besoins des clients, la conception et la mise en place de solutions MES adaptes  leurs exigences spcifiques, ainsi que dans la coordination et la supervision des quipes impliques.</p>
<p className="scroll-animation" data-aos='fade-up'>
Mon exprience approfondie dans les tests et la validation automatise m'a permis de dvelopper des stratgies et des processus efficaces pour assurer la qualit et la fiabilit des systmes et des logiciels industriels. J'ai galement acquis une solide exprience en matire de support technique, en aidant les quipes et les clients  rsoudre les problmes techniques et  assurer un fonctionnement optimal des systmes.</p>
<p className="scroll-animation" data-aos='fade-up'>
Je suis constamment  l'afft des dernires avances technologiques et des tendances de l'industrie 4.0. Je suis convaincu que la transformation numrique est essentielle pour permettre aux entreprises de rester comptitives dans un environnement en constante volution.</p>
<p className="scroll-animation" data-aos='fade-up'>
Je suis passionn par mon travail et je m'engage  fournir des solutions innovantes et  atteindre les objectifs fixs. Je suis ouvert  de nouvelles opportunits et  des projets passionnants dans le domaine de l'industrialisation, de la digitalisation et de la smart industrie 4.0.</p>
            </div>
        </div>
    </section>
  )
}
//<h1 className="scroll-animation" data-aos='fade-up'>Every great design begin with<br/>
//an even <span>better story</span></h1>